export interface GetEligibleDiscountsRequest {
  /**
   * Line items
   * @maxSize 300
   */
  lineItems?: LineItem[];
  /**
   * Persistent ID that correlates between the various eCommerce elements: cart, checkout, and order.
   * @format GUID
   */
  purchaseFlowId?: string | null;
  /**
   * Number of coupons is limited to 1 due to limitations in Checkout.
   * @maxSize 1
   * @maxLength 20
   */
  couponCodes?: string[];
  /**
   * Buyer email
   * @format EMAIL
   */
  buyerEmail?: string | null;
}

export interface LineItem {
  /**
   * Line item ID.
   * @format GUID
   */
  _id?: string;
  /**
   * Line item quantity.
   * @max 100000
   */
  quantity?: number | null;
  /** Catalog and item reference info. Learn more about [integrating Wix Stores products with Wix eCommerce](https://dev.wix.com/api/rest/wix-stores/catalog/ecommerce-integration). */
  catalogReference?: CatalogReference;
  /**
   * Line item price.
   * @format DECIMAL_VALUE
   * @decimalValue options { gte:0, maxScale:6 }
   */
  price?: string;
}

/** Used for grouping line items. Sent when an item is added to a cart, checkout, or order. */
export interface CatalogReference {
  /**
   * ID of the item within the catalog it belongs to.
   * @minLength 1
   * @maxLength 36
   */
  catalogItemId?: string;
  /**
   * ID of the app providing the catalog.
   *
   * You can get your app's ID from its page in the [app dashboard](https://dev.wix.com/dc3/my-apps/).
   *
   * For items from Wix catalogs, the following values always apply:
   * + Wix Stores: `"215238eb-22a5-4c36-9e7b-e7c08025e04e"`
   * + Wix Bookings: `"13d21c63-b5ec-5912-8397-c3a5ddb27a97"`
   * + Wix Restaurants: `"9a5d83fd-8570-482e-81ab-cfa88942ee60"`
   * @minLength 1
   */
  appId?: string;
  /**
   * Additional item details in key:value pairs. Use this optional field to provide more specificity with item selection. The `options` field values differ depending on which catalog is providing the items.
   *
   * For products and variants from a Wix Stores catalog, learn more about eCommerce integration ([SDK](https://dev.wix.com/docs/sdk/backend-modules/stores/catalog-v3/e-commerce-integration) | [REST](https://dev.wix.com/docs/rest/business-solutions/stores/catalog/e-commerce-integration)).
   */
  options?: Record<string, any> | null;
}

export interface GetEligibleDiscountsResponse {
  /**
   * List of discounts to apply
   * @maxSize 100
   */
  discounts?: Discount[];
}

/** Discount */
export interface Discount
  extends DiscountDiscountTypeOneOf,
    DiscountDiscountScopesOneOf {
  /** Global discounts */
  globalDiscounts?: BaseDiscounts;
  /** Shipping discounts */
  shippingDiscounts?: BaseDiscounts;
  /** line items discounts */
  lineItemsDiscounts?: LineItemsDiscounts;
  /** Coupon options */
  couponOptions?: CouponOptions;
  /** Target type */
  targetType?: TargetType;
  /** Name */
  name?: DiscountName;
  /**
   * external id used by the implementor
   * @maxLength 50
   */
  externalId?: string | null;
  /** Source */
  source?: DiscountSource;
}

/** @oneof */
export interface DiscountDiscountTypeOneOf {
  /** Global discounts */
  globalDiscounts?: BaseDiscounts;
  /** Shipping discounts */
  shippingDiscounts?: BaseDiscounts;
  /** line items discounts */
  lineItemsDiscounts?: LineItemsDiscounts;
}

/** @oneof */
export interface DiscountDiscountScopesOneOf {
  /** Coupon options */
  couponOptions?: CouponOptions;
}

export enum TargetType {
  UNKNOWN_TARGET = 'UNKNOWN_TARGET',
  GLOBAL = 'GLOBAL',
  SHIPPING = 'SHIPPING',
  LINE_ITEMS = 'LINE_ITEMS',
}

export interface DiscountName {
  /**
   * Original discount rule name (in site's default language).
   * @minLength 1
   * @maxLength 256
   */
  original?: string;
  /**
   * Translated discount rule name according to buyer language.
   *
   * Default: `original`
   * @minLength 1
   * @maxLength 500
   */
  translated?: string | null;
}

export interface BaseDiscounts {
  /** The rate of the discount. */
  rate?: DiscountRate;
}

export interface DiscountRate extends DiscountRateRateOneOf {
  /**
   * Global discounts
   * @decimalValue options { gte:0, maxScale:6 }
   */
  fixedAmountValue?: string;
  /**
   * Shipping discounts
   * @decimalValue options { gte:0, maxScale:6 }
   */
  percentageValue?: string;
  /** Type of calculation */
  type?: Type;
}

/** @oneof */
export interface DiscountRateRateOneOf {
  /**
   * Global discounts
   * @decimalValue options { gte:0, maxScale:6 }
   */
  fixedAmountValue?: string;
  /**
   * Shipping discounts
   * @decimalValue options { gte:0, maxScale:6 }
   */
  percentageValue?: string;
}

export enum Type {
  UNKNOWN_RATE = 'UNKNOWN_RATE',
  FIXED_AMOUNT = 'FIXED_AMOUNT',
  PERCENTAGE = 'PERCENTAGE',
}

export interface LineItemsDiscounts {
  /**
   * Line items
   * @maxSize 300
   */
  lineItems?: LineItemRate[];
}

export interface LineItemRate {
  /** The rate of the discount. */
  rate?: DiscountRate;
  /**
   * id of the line item.
   * @format GUID
   */
  _id?: string;
}

export enum DiscountSource {
  UNKNOWN_SOURCE = 'UNKNOWN_SOURCE',
  COUPON = 'COUPON',
  AUTOMATIC = 'AUTOMATIC',
  CUSTOM = 'CUSTOM',
}

export interface CouponOptions {
  /**
   * Coupon code, only relevant when source is coupon.
   * @maxLength 20
   */
  code?: string | null;
}

export interface DiscountsSPIConfig {
  /**
   * Base URI where the endpoints are called.
   * Wix eCommerce appends the endpoint path to the base URI.
   * @minLength 1
   * @maxLength 200
   */
  deploymentUri?: string;
}

/**
 * this message is not directly used by any service,
 * it exists to describe the expected parameters that SHOULD be provided to invoked Velo methods as part of open-platform.
 * e.g. SPIs, event-handlers, etc..
 * NOTE: this context object MUST be provided as the last argument in each Velo method signature.
 *
 * Example:
 * ```typescript
 * export function wixStores_onOrderCanceled({ event, metadata }: OrderCanceledEvent) {
 * ...
 * }
 * ```
 */
export interface Context {
  /** A unique identifier of the request. You may print this ID to your logs to help with future debugging and easier correlation with Wix's logs. */
  requestId?: string | null;
  /**
   * [ISO 4217](https://en.wikipedia.org/wiki/ISO_4217) 3-letter currency code.
   * @format CURRENCY
   */
  currency?: string | null;
  /** An object that describes the identity that triggered this request. */
  identity?: IdentificationData;
  /** A string representing a language and region in the format of `"xx-XX"`. First 2 letters represent the language code according to ISO 639-1. This is followed by a dash "-", and then a by 2 capital letters representing the region according to ISO 3166-2. For example, `"en-US"`. */
  languages?: string[];
  /**
   * The service provider app's instance ID.
   * @format GUID
   */
  instanceId?: string | null;
}

export enum IdentityType {
  UNKNOWN = 'UNKNOWN',
  ANONYMOUS_VISITOR = 'ANONYMOUS_VISITOR',
  MEMBER = 'MEMBER',
  WIX_USER = 'WIX_USER',
  APP = 'APP',
}

export interface IdentificationData extends IdentificationDataIdOneOf {
  /**
   * ID of a site visitor that has not logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor that has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user (site owner, contributor, etc.).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
  /** @readonly */
  identityType?: IdentityType;
}

/** @oneof */
export interface IdentificationDataIdOneOf {
  /**
   * ID of a site visitor that has not logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor that has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user (site owner, contributor, etc.).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
}
